import React, { useState } from 'react';
import axios from 'axios';
import './App.css';
import { text } from 'body-parser';

function App() {
    const [isSignIn, setIsSignIn] = useState(true);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [name, setName] = useState('');
    const [error, setError] = useState('');

    // Function to handle sign in
    const handleEmail = (e) =>{
        setEmail({...email,[e.target.email]: e.target.value})
    }
    const handlePass = (e) =>{
        setPassword({...password,[e.target.password]: e.target.value})
    }
    const handleName = (e) =>{
        setName({...text,[e.target.text]: e.target.value})
    }
    const handleSignIn = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post('/user/login', { email, password });
            console.log(response.data);
            // Handle successful login (e.g., redirect or show a success message)
        } catch (err) {
            setError('Invalid email or password');
            console.error(err);
        }
    };

    // Function to handle sign up
    const handleSignUp = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post('/user/signup', { name, email, password });
            console.log(response.data);
            // Handle successful sign up (e.g., redirect or show a success message)
        } catch (err) {
            setError('Error signing up, please try again');
            console.error(err);
        }
    };

    return (
        <div className="container">
            {error && <div className="error-message">{error}</div>}

            {/* Sign In Form */}
            <div className={`form-container sign-in-container ${isSignIn ? 'active' : ''}`}>
                <form onSubmit={handleSignIn}>
                    <h1>Sign In</h1>
                    <input
                        type="email"
                        placeholder="Email"
                        value={email}
                        // onChange={(e) => setEmail(e.target.value)}
                        onChange={handleEmail}
                        required
                    />
                    <input
                        type="password"
                        placeholder="Password"
                        value={password}
                        // onChange={(e) => setPassword(e.target.value)}
                        onChange={handlePass}
                        required
                    />
                    <button type="submit">Sign In</button>
                </form>
            </div>

            {/* Sign Up Form */}
            <div className={`form-container sign-up-container ${!isSignIn ? 'active' : ''}`}>
                <form onSubmit={handleSignUp}>
                    <h1 id="create">Create Account</h1>
                    <input
                        type="text"
                        placeholder="Name"
                        value={name}
                        // onChange={(e) => setName(e.target.value)}
                        onChange={handleName}
                        required
                    />
                    <input
                        type="email"
                        placeholder="Email"
                        value={email}
                        // onChange={(e) => setEmail(e.target.value)}
                        onChange={handleEmail}
                        required
                    />
                    <input
                        type="password"
                        placeholder="Password"
                        value={password}
                        // onChange={(e) => setPassword(e.target.value)}
                        onChange={handlePass}
                        required
                    />
                    <button type="submit">Sign Up</button>
                </form>
            </div>
        </div>
    );
}

export default App;
